          p.mb-0 Con la ayuda del diseño gráfico, las empresas turísticas pueden desarrollar contenido de comunicación visual que atraiga a más usuarios. El responsable del diseño gráfico de una empresa es, entre otras cosas, responsable de definir paleta de colores, formas y tipografías para lograr una comunicación clara y atractiva a través de medios gráficos.
    
    p.mt-5(data-aos="zoom-in") El diseño gráfico tiene varios elementos que componen la comunicación visual, proyecta y expresa diferentes mensajes visuales. A continuación se presentan las formas básicas del diseño gráfico: puntos, líneas y contornos.

    ul.fa-ul.mt-4
      li.mb-2(data-aos="zoom-in")
        i.fas.fa-check.fa-li(style="color:#ED7D41;")
        | El punto: es el elemento gráfico básico y conforma la unidad mínima de la comunicación visual.
      li.mb-2(data-aos="zoom-in")
        i.fas.fa-check.fa-li(style="color:#ED7D41;")
        | La línea, la sucesión de varios puntos en el espacio. 
      li(data-aos="zoom-in")
        i.fas.fa-check.fa-li(style="color:#ED7D41;")
        | El contorno es el conjunto de líneas que limitan una forma y se pueden hacer infinitas combinaciones con ellas.

    p.mt-5(data-aos="zoom-in") Cada uno de estos elementos tiene sus propias características que permiten modificarlos. Las que más predominan son:

