      .col-lg-4.col-md-6.mb-5
        .h-100.p-4.p-md-5.pt-3.text-center(style="background-color:#FFF4E1;box-shadow:0px 4px 8px rgba(0,0,0,.1);border-radius:19px;" data-aos="flip-up")
