      h4.mb-0.ps-2.pe-4 Psicología del color
    
    p(data-aos="zoom-in") Las empresas de turismo deben analizar con mucho cuidado los colores utilizados en sus diseños, ya que afectan la parte emocional de los humanos. Hay algunas características de color a considerar para crear un buen diseño, tal como se muestra en el siguiente video, donde se explica como la psicología del color se aplica a la publicidad: 

