    .titulo-segundo
      h2#t_1_3 1.3  Otras herramientas de diseño
    
    p(data-aos="zoom-in") Las herramientas de diseño son una serie de programas utilizados para el diseño asistido por ordenador. Normalmente, se usan las siglas CAD (Computer Aided Design) para denominarlas y sustituyeron a los viejos métodos manuales que se usaban hasta la aparición de la informática (Madrid, 2014), en el siguiente recurso, se encontrarán otras alternativas de herramientas para el diseño: 
    
    TabsC.color-primario.mt-5
      .py-3.py-md-4(titulo="Photoshop")
        .row
          .col-md-6.mb-4.mb-md-0(style="place-self:center;")
            figure.text-center
              img(src='@/assets/curso/tema1/14.svg' style="display:inline;max-width:470px;" alt="Imagen decorativa")
          .col-md-6(style="place-self:center;")
            h5 Photoshop
            p Adobe Photoshop es la herramienta principal para crear, editar y retocar imágenes de mapa de bits. Se trata de imágenes basadas en una cuadrícula ortogonal de colores, cuya representación más pequeña son los llamados píxeles, que son pequeños cuadrados que aparecen cuando aumentas el tamaño de la foto. Juntos, estos píxeles agregan forma, color e intensidad a la imagen. 
            p.mt-3 Photoshop incorpora en su desarrollo algunas características increíbles que lo catalogan como un programa realmente poderoso. Utilizado por diseñadores, ilustradores y fotógrafos.
      .py-3.py-md-4(titulo="Illustrator")
        .row
          .col-md-6.mb-4.mb-md-0(style="place-self:center;")
            figure.text-center
              img(src='@/assets/curso/tema1/15.svg' style="display:inline;max-width:470px;" alt="Imagen decorativa")
          .col-md-6(style="place-self:center;")
            h5 Illustrator
            p Adobe Illustrator es el #[em software] Adobe Creative Cloud, pero también es una herramienta básica de diseño vectorial, este es un programa dedicado a crear dibujos vectoriales y diseñar elementos gráficos para usar en varios soportes y dispositivos. Es una herramienta básica para el diseño editorial, ilustración, gráficos, maquetación e interfaces web. 
            p.mt-3 Contiene una serie de herramientas que todos los diseñadores gráficos utilizan para crear y manipular imágenes mediante la vectorización. Las imágenes vectorizadas se componen de puntos en el espacio virtual conectados por rutas, lo que da como resultado imágenes a gran escala y de alta calidad.
      .py-3.py-md-4(titulo="Canva")
        .row
          .col-md-6.mb-4.mb-md-0(style="place-self:center;")
            figure.text-center
              img(src='@/assets/curso/tema1/16.svg' style="display:inline;max-width:380px;" alt="Imagen decorativa")
          .col-md-6(style="place-self:center;")
            h5 Canva
            p Es una herramienta gratuita, solo se debe hacer el registro para comenzar a diseñar. Incluye un tutorial como guía para principiantes. Es importante señalar que además de la versión gratuita, existe una versión "pro" paga. Incluye imágenes, plantillas y otros recursos. Canva tiene una biblioteca muy grande y diversa de más de 1 millón de activos, que incluyen plantillas, fotos, gráficos, ilustraciones e íconos.
            p.mt-3 Tiene un motor de búsqueda para encontrar las imágenes precisas según la necesidad, e incluye la opción de subir fotos del computador o de Facebook. Una vez seleccionada una plantilla, se puede modificar lo que se requiera. Los elementos están en capas y agrupados, pero puede desagruparse para editarlos individualmente.
    
    Separador.mt-5

    .titulo-segundo
      h2#t_1_4 1.4  Herramienta para la creación de audio
    
    p(data-aos="zoom-in") Las empresas de turismo también pueden utilizar el sonido y la música como estrategia de #[em marketing] creando audio para compartir con sus usuarios. Algunos de los beneficios del #[em marketing] auditivo son:

    .row.mt-4(data-aos="zoom-in")
      .col-auto.pe-0(style="place-self:center;")
        figure
          img(src="@/assets/curso/tema1/18.svg" alt="Imagen decorativa")
      .col(style="place-self:center;")
        p.mb-0 #[strong Recuerdo de marca:] el 72 % de las personas recuerda los sonidos y melodías de la marca. 
    .row.mt-2(data-aos="zoom-in")
      .col-auto.pe-0(style="place-self:center;")
        figure
